%paginatorStyle {
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 0 5px 0 0;
  background-color: #333752;
  color: #565656;
  border: none;
  border-radius: 4px;
  box-shadow: inset 0 5px 10px rgba(0, 0, 0, .1), 0 2px 5px rgba(0, 0, 0, .5);
  cursor: pointer;
  &:hover {
    color: #fff;
    text-shadow: 0 0 8px rgb(255, 224, 27),
    0 0 8px rgb(255, 224, 27),
    0 0 8px rgb(255, 224, 27),
    0 0 8px rgb(255, 224, 27),
    0 0 8px rgb(255, 224, 27),
    0 0 8px rgb(255, 224, 27);
  }
}
.pages {
  width: 100%;
  display: flex;
  align-items: center;
  margin: 10px 10px 20px 70px;
  & > button {
    width: 25px;
    height: 25px;
    &:first-child{
      margin: 0 20px 0 0;
    }
    &:last-child{
      margin: 0 0 0 10px;
    }
    @extend %paginatorStyle;
  }
}
.page, .select {
  width: 30px;
  height: 30px;
  @extend %paginatorStyle;

}
.select {
  color: #fff;
  text-shadow: 0 0 8px rgb(255, 224, 27),
  0 0 8px rgb(255, 224, 27),
  0 0 8px rgb(255, 224, 27),
  0 0 8px rgb(255, 224, 27),
  0 0 8px rgb(255, 224, 27),
  0 0 8px rgb(255, 224, 27);
}
