.wrapper {
  display: flex;
  margin: 5px 0 10px 0;
}

.ava{
  width: 50px;
  height: 50px;
  margin: 0 20px 20px 0;
  & >button{
    width: 100%;
    height: 15px;
    text-transform: uppercase;
    text-align: center;
    font-size: 8px;
    font-weight: bold;
    padding: 0;
  }
}
.user_link{
  & >img{
    width: 100%;
    border-radius: 50%;
    overflow: hidden;
  }
}
.description{
  width: 450px;
  height: 60px;
  display: flex;
  border:1px solid #000000;
  border-radius: 5px;
  display: flex;
  justify-content: space-between;
  padding: 5px;
}
.user{
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.fullname{
  font-size: 18px;
  font-weight: bold;
}
.status{
  font-size: 12px;
}
.location{
  font-size: 14px;
}
.city{
  margin: 0 0 5px 0;
}
